import React, { Component } from 'react'
import Nav from './NavBar'

export default class Explore extends Component {
  
    render() {
        return (
            <div>
                <Nav></Nav>
                <main>
	<div className="menu">

		<div className="panel">
			<label className="text" for="toggle">Admin Settings</label>
			<label className="icon fas fa-cog" for="toggle"></label>
		</div>

		<input type="checkbox" id="toggle"/>

		<div className="dropdown">
			<div className="arrow"></div>

			<a href="#" className="row">
				<div className="text">Edit User</div>
				<i className="icon fas fa-user"></i>
			</a>
			<a href="#" className="row">
				<div className="text">Web Statistics</div>
				<i className="icon far fa-chart-bar"></i>
			</a>
			<a href="#" className="row">
				<div className="text">Upload Settings</div>
				<i className="icon fas fa-upload"></i>
			</a>
			<a href="#" className="row">
				<div className="text">Edit Sliders</div>
				<i className="icon fas fa-pencil-alt"></i>
			</a>
		</div>

	</div>
</main>
            </div>
        )
    }
}
